import React from "react";
import AdminLayout from "../../components/Layouts/AdminLayout";
import VoiceInterface from "../../components/VoiceInterface/VoiceInterface";
import CardStats from "../../components/Cards/CardStats";

export default function Dashboard() {
  return (
    <AdminLayout>
      <div className="flex flex-wrap">
        <div className="w-full mb-12 px-4">
          {/* Stats Cards */}
          <div className="flex flex-wrap">
            <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
              <CardStats
                statSubtitle="Active Sessions"
                statTitle="24"
                statArrow="up"
                statPercent="12"
                statPercentColor="text-emerald-500"
                statDescription="Since last hour"
                statIconName="fas fa-users"
                statIconColor="bg-pink-500"
              />
            </div>
            <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
              <CardStats
                statSubtitle="Questions Answered"
                statTitle="156"
                statArrow="up"
                statPercent="23"
                statPercentColor="text-emerald-500"
                statDescription="Since yesterday"
                statIconName="fas fa-comments"
                statIconColor="bg-blue-500"
              />
            </div>
            <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
              <CardStats
                statSubtitle="Crisis Interventions"
                statTitle="3"
                statArrow="down"
                statPercent="8"
                statPercentColor="text-red-500"
                statDescription="Since last week"
                statIconName="fas fa-heart"
                statIconColor="bg-red-500"
              />
            </div>
            <div className="w-full lg:w-6/12 xl:w-3/12 px-4">
              <CardStats
                statSubtitle="Satisfaction Rate"
                statTitle="94%"
                statArrow="up"
                statPercent="3"
                statPercentColor="text-emerald-500"
                statDescription="Since last month"
                statIconName="fas fa-star"
                statIconColor="bg-yellow-500"
              />
            </div>
          </div>
        </div>
      </div>

      {/* Voice Interface */}
      <div className="flex flex-wrap mt-6">
        <div className="w-full mb-12 px-4">
          <VoiceInterface />
        </div>
      </div>

      {/* Quick Actions */}
      <div className="flex flex-wrap mt-6">
        <div className="w-full mb-12 px-4">
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h3 className="text-xl font-semibold text-gray-800 mb-4">
              Quick Actions
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <button className="p-4 bg-blue-50 hover:bg-blue-100 rounded-lg border border-blue-200 transition-colors">
                <i className="fas fa-book text-2xl text-blue-600 mb-2"></i>
                <div className="font-medium text-blue-800">Financial Literacy</div>
                <div className="text-sm text-blue-600">Learn the basics</div>
              </button>
              <button className="p-4 bg-green-50 hover:bg-green-100 rounded-lg border border-green-200 transition-colors">
                <i className="fas fa-calculator text-2xl text-green-600 mb-2"></i>
                <div className="font-medium text-green-800">Budget Planning</div>
                <div className="text-sm text-green-600">Track expenses</div>
              </button>
              <button className="p-4 bg-purple-50 hover:bg-purple-100 rounded-lg border border-purple-200 transition-colors">
                <i className="fas fa-shield-alt text-2xl text-purple-600 mb-2"></i>
                <div className="font-medium text-purple-800">Crisis Support</div>
                <div className="text-sm text-purple-600">Get immediate help</div>
              </button>
              <button className="p-4 bg-orange-50 hover:bg-orange-100 rounded-lg border border-orange-200 transition-colors">
                <i className="fas fa-chart-line text-2xl text-orange-600 mb-2"></i>
                <div className="font-medium text-orange-800">Progress Tracking</div>
                <div className="text-sm text-orange-600">View your journey</div>
              </button>
            </div>
          </div>
        </div>
      </div>
    </AdminLayout>
  );
}
